<template>
  <div id="app" tyle="overflow-x: hidden;">
      <b-nav class="nav-bar" align="center">
        <b-nav-item to="/skills"
          ><p class="zoom nav-title">skills</p></b-nav-item
        >
        <b-nav-item to="/work"><p class="zoom nav-title">work</p></b-nav-item>
        <b-nav-item to="/studies"
          ><p class="zoom nav-title">studies</p></b-nav-item
        >
        <b-nav-item v-if="!isMobile" to="/games"
          ><p class="zoom nav-title">games</p></b-nav-item
        >
      </b-nav>
  
    <Home class="box-padding" style="padding-top: 60px;" />
    <About class="box-padding bg-light-green2" />
    <Skills id="skills" class="bg-light-green box-padding" />
    <Timeline
      id="work"
      timelineClass="work"
      :isWork="true"
      class="bg-dark-green box-padding"
    />
    <Timeline
      id="studies"
      timelineClass="studies"
      :isWork="false"
      class="bg-light-green2 box-padding"
    />
    <Phone v-if="!isMobile" id="games" class="bg-light-green box-padding" />
    <i type="button" class="fas fa-arrow-alt-circle-up fa-2x zoom" style="position: fixed; bottom: 15px; right: 15px; color:var(--white)" v-on:click="scrollTop" ></i> 
  </div>
</template>

<script>
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import Timeline from "./views/Timeline.vue";
import Skills from "./views/Skills.vue";
import Phone from "./views/Phone.vue";

export default {
  data() {
    return { windowWidth: window.innerWidth };
  },
  components: {
    Home,
    About,
    Timeline,
    Skills,
    Phone,
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
    });
  },
  computed: {
    isMobile() {
      return this.windowWidth <= 768;
    },
  },
  beforeDestroy() {
    window.removeEventListener("resize");
  },
  methods: {
    scrollTop() {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  }
};
</script>

<style>
@import "./assets/css/global.css";
#app {
  background-color: var(--dark-green);
}

.nav-title {
  font-family: "Amatic SC", Bold;
  font-size: 32px;
  color: var(--white) !important;
}
.nav-title:hover {
  color: var(--white);
}
.construction {
  position: absolute;
  top: 5px;
  left: 5px;
  font-size: 24px !important;
}
@media (max-width: 768px) {
  .construction {
    display: none;
  }
}
</style>
