<template>
  <div class="center-horizontal center-vertical">
    <div>
      <h3
        class="header-margin-bottom"
        data-aos="fade-right"
        data-aos-duration="900"
      >
        some words about me
      </h3>

      <div class="row ">
        <div class="col-sm-auto center-horizontal">
          <img src="../assets/images/pic2.png" alt="" />
        </div>
        <div
          class="col"
          v-bind:style="
            isMobile
              ? 'text-align: center; margin: 5px;'
              : 'text-align: start; margin-left: 5px;'
          "
        >
          <p class="medium" data-aos="fade-left" data-aos-duration="900">
            I'm a creative girl from sweden.
          </p>
          <p class="medium" data-aos="fade-left" data-aos-duration="500">
            I love programming, art, games and working out.
          </p>
          <p class="medium" data-aos="fade-left" data-aos-duration="1300">
            Three words to describe me would be: happy, curious and stubborn.
          </p>
          <p class="medium" data-aos="fade-left" data-aos-duration="700">
            Sometimes I stream on twitch.
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return { windowWidth: window.innerWidth };
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowWidth = window.innerWidth;
    });
  },
  computed: {
    isMobile() {
      return this.windowWidth <= 768;
    },
  },
  beforeDestroy() {
    window.removeEventListener("resize");
  },
};
</script>

<style scoped>
p {
  padding-top: 0 !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

img {
  border-radius: 5px;
}
</style>
