<template>
  <div style="height: 100%">
    <div
      v-if="!playing"
      class="center-vertical center-horizontal"
      style="height: 100%;"
    >
      <div>
        <div v-if="firstTime">
          <p class="small">collect colors to keep the snake small.</p>
          <p class="small">
            move with <span style="font-size: 28px">W A S D</span>
          </p>
        </div>
        <div v-else>
          <p class="small">You survived for {{ timeAlive }} seconds!</p>
        </div>
        <span @click="onStartClick">
          <p class="medium btn zoom" style="margin-top: 20px;">START</p>
        </span>
      </div>
    </div>
    <div v-else>
      <p class="small">time alive: {{ timeAlive }}</p>
      <div class="center-align-vertical">
        <div class="board">
          <Block
            v-for="(block, index) in blocks"
            :key="index"
            :x="block.x"
            :y="block.y"
            :color="block.color"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Block from "@/components/games/snake/Block";
import GameFunctions from "@/assets/gamedata/GameData.js";

const rows = 13;
const columns = 9;
const candy1 = "#6a4c93";
const candy2 = "#1982c4";
const candy3 = "#8ac926";
const transparent = "transparent";
const snakeColor = "#2c2d34";

var countTillNextBodyChop;
var interval;
var blocks = [];
var snake = [];
var direction;
var nextPosition;
var playing;
var timeAlive;
var firstTime = true;

export default {
  data() {
    return {
      blocks,
      snake,
      direction,
      nextPosition,
      countTillNextBodyChop,
      playing,
      timeAlive,
      firstTime,
    };
  },
  components: {
    Block,
  },
  created() {
    window.addEventListener("keypress", this.onKeyPress);
    this.firstTime = true;
    this.playing = false;
  },
  destroyed() {
    clearInterval(interval);
    this.playing = false;
    window.removeEventListener("keypress", this.onKeyPress);
  },
  methods: {
    update() {
      nextPosition = this.getNextPosition();
      if (this.isOutsideOfBounds(nextPosition)) {
        this.gameOver();
      }

      this.moveSnake();
      this.tryCreateCandy();
      countTillNextBodyChop--;

      if (countTillNextBodyChop <= 0) {
        this.chopSnakeBody();
        countTillNextBodyChop = 3;
      }
    },
    onKeyPress(e) {
      if (!this.playing) return;
      else this.setNextDirection(String.fromCharCode(e.keyCode).toLowerCase());
    },
    onStartClick() {
      this.startGame();
    },
    setNextDirection(key) {
      switch (key) {
        case "a":
          direction = { x: -1, y: 0 };
          break;
        case "d":
          direction = { x: 1, y: 0 };
          break;
        case "w":
          direction = { x: 0, y: -1 };
          break;
        case "s":
          direction = { x: 0, y: 1 };
          break;
      }
    },
    startGame() {
      this.setStartValues();

      if (blocks.length > 0) {
        this.resetBlocks();
      } else {
        this.createBlocks();
      }

      this.playing = true;
      interval = setInterval(this.update, 200);
      this.timeAliveTick();
    },
    gameOver() {
      this.playing = false;
      this.firstTime = false;
      clearInterval(interval);
    },
    moveSnake() {
      let nextBlock = blocks.find(
        (b) => b.x == nextPosition.x && b.y == nextPosition.y
      );
      if (nextBlock) {
        if (nextBlock.color == snakeColor) this.gameOver();
        else if (nextBlock.color != transparent) this.eatCandy(nextBlock.color);

        nextBlock.color = snakeColor;
        snake.push(nextBlock);
      }
    },
    eatCandy(color) {
      let amount = this.getAmountFromColor(color);

      for (let i = 0; i < amount; i++) {
        if (snake.length == 1) return;
        this.chopSnakeBody();
      }
    },
    chopSnakeBody() {
      let block = snake.shift();
      block.color = transparent;
    },
    getAmountFromColor(color) {
      return color == candy1 ? 1 : color == candy2 ? 2 : 6;
    },
    tryCreateCandy() {
      let candyNumber = GameFunctions.getRandomNumber(0, 20);

      if (
        candyNumber == 1 ||
        candyNumber == 2 ||
        candyNumber == 3 ||
        candyNumber == 4
      ) {
        this.createCandy(candy1);
        return;
      }
      if (candyNumber == 5 || candyNumber == 6) {
        this.createCandy(candy2);
        return;
      }
      if (candyNumber == 7) {
        this.createCandy(candy3);
        return;
      }
    },
    createCandy(color) {
      let x = GameFunctions.getRandomNumber(0, columns);
      let y = GameFunctions.getRandomNumber(0, rows);

      let block = blocks.find((b) => b.x == x && b.y == y);
      if (block.color != snakeColor) block.color = color;
      else this.createCandy(color);
    },
    createBlocks() {
      for (let y = 0; y < rows; y++) {
        for (let x = 0; x < columns; x++) {
          let block = {
            x: x,
            y: y,
            color: x == 3 && y == 3 ? snakeColor : transparent,
          };
          blocks.push(block);
          if (block.color == snakeColor) snake.push(block);
        }
      }
    },
    resetBlocks() {
      for (let i = 0; i < this.blocks.length; i++) {
        blocks[i].color = transparent;
      }
    },
    setStartValues() {
      snake = [];
      direction = { x: 1, y: 0 };
      nextPosition = { x: 3, y: 3 };
      countTillNextBodyChop = 3;
      this.timeAlive = 0;
    },
    getNextPosition() {
      return (nextPosition = {
        x: nextPosition.x + direction.x,
        y: nextPosition.y + direction.y,
      });
    },
    isOutsideOfBounds(position) {
      return (
        position.x >= columns ||
        position.x < 0 ||
        position.y >= rows ||
        position.y < 0
      );
    },
    timeAliveTick() {
      setTimeout(() => {
        if (!this.playing) return;
        this.timeAlive++;
        this.timeAliveTick();
      }, 1000);
    },
  },
};
</script>

<style scoped>
.board {
  border-top: 2px solid var(--gray);
  display: grid;
  grid-template-columns: repeat(9, 47px);
  grid-template-rows: repeat(13, 47px);
  grid-column-gap: 2px;
  grid-row-gap: 2px;
}
.wasd {
  display: grid;
  grid-template-columns: repeat(3, 40px);
  grid-template-rows: repeat(2, 40px);
  grid-column-gap: 2px;
  grid-row-gap: 2px;
}
.center-align-vertical {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}

p {
  color: var(--gray) !important;
  margin: 0px;
  padding: 0px;
}
</style>
