<template>
  <div class="games-container center-horizontal center-vertical">
    <div>
      <h3
        class="header-margin-bottom"
        data-aos="fade-right"
        data-aos-duration="900"
      >
        some games
      </h3>

      <div class="row center-vertical">
        <div class="col">
          <h4 data-aos="fade-right" data-aos-duration="500">mobile games</h4>
          <span class="btn zoom break">
            <p
              data-aos="fade-right"
              data-aos-duration="1500"
              @click="startPlaying(1)"
            >
              snake
            </p>
          </span>
          <span class="btn zoom">
            <p
              data-aos="fade-right"
              data-aos-duration="900"
              @click="startPlaying(2)"
            >
              color match
            </p>
          </span>

          <h4 data-aos="fade-right" data-aos-duration="800">webpages</h4>
          <a
            href="https://www.dodrink.net/"
            target="blank"
            class="btn zoom menu-item"
          >
            <p data-aos="fade-right" data-aos-duration="1200">Drinking game</p>
          </a>
        </div>
        <div class="col center-horizontal">
          <div class="phone" data-aos="fade-left" data-aos-duration="500">
            <div class="screen">
              <div
                v-if="!playing"
                class="center-vertical center-horizontal"
                style="height: 100%;"
              >
                <i class="fas fa-smile-beam fa-10x phone-hidden"></i>
              </div>
              <component v-else v-bind:is="currentGame" />
            </div>
            <div class="center-horizontal center-vertical">
              <div class="phone-button btn" @click="stopPlaying"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ColorMatch from "@/components/games/colormatch/ColorMatch";
import Snake from "@/components/games/snake/Snake";
export default {
  data() {
    return { currentGame: ColorMatch, playing: false };
  },
  components: {
    ColorMatch,
    Snake,
  },
  methods: {
    stopPlaying() {
      this.playing = false;
    },
    startPlaying(index) {
      switch (index) {
        case 1:
          if (window.innerWidth <= 768) return;
          this.currentGame = Snake;
          break;
        case 2:
          this.currentGame = ColorMatch;
          break;
      }
      this.playing = true;
    },
    goToPage(page) {
      window.location.href = page;
    },
  },
};
</script>

<style scoped>
.phone-button {
  background-color: var(--white);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-top: 35px;
  box-shadow: var(--gray2) 3px 5px;
}
hr {
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 0px;
  padding: 0px;
  border-top: 2px solid var(--gray);
}
p {
  margin: 0;
  padding: 0;
}
img {
  background-color: var(--light-green);
  border-radius: 20px;
  padding: 10px;
  width: 80px;
}
.menu-header {
  margin-top: 25px;
  padding: 0;
  margin-bottom: 0;
}
.menu-item {
  margin-top: 5px;
  display: block;
  border: none !important;
}
.menu-item:focus {
  border: none !important;
}
.screen {
  color: var(--gray);
  margin-top: 4vh;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: var(--white);
}
.phone {
  display: grid;
  grid-template-rows: 85% auto;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 40px;
  height: 800px;
  max-width: 490px;
  width: 85vw;
  background-color: var(--gray);
  box-shadow: var(--gray2) 5px 8px;
}
.games-container {
  min-height: 100vh;
}
</style>
