<template>
  <div :id="id" class="zoom" style="position: relative;">
    <b-tooltip v-if="!disabled" :target="id" triggers="hover">{{
      description
    }}</b-tooltip>
    <button @click="onClick" :disabled="disabled">
      <i
        :class="bonus"
        :style="{ color: disabled ? '#4e4f58' : color ? color : '' }"
      ></i>
    </button>
    <b-badge v-if="!disabled" class="amount">{{ amount }}</b-badge>
  </div>
</template>

<script>
export default {
  props: {
    id: String,
    bonus: String,
    amount: Number,
    description: String,
    color: String,
  },
  computed: {
    disabled() {
      return this.amount ? this.amount <= 0 : true;
    },
  },
  methods: {
    onClick() {
      this.$emit("on-bonus-click", {
        id: this.id,
        bonus: this.bonus,
        color: this.color,
      });
    },
  },
};
</script>

<style scoped>
button {
  width: 40px;
  height: 40px;
  border-style: none;
  background-color: transparent;
}
.amount {
  background-color: var(--gray);
  padding: 2px;
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 10px;
  font-family: arial;
}
</style>
